.log{padding:3px 8px 5px 8px;flex-shrink:0;display:flex;flex-direction:column}.log .header{font-family:arial,sans-serif;flex-basis:3ex;flex-shrink:0;user-select:none}.log .header .controls{display:flex;align-items:center;justify-content:space-between;line-height:22px}.log .header .controls .title{font-size:130%;font-weight:bold}.log .header .controls .buttons{display:flex;align-items:center}.log .header .controls .buttons .emoji-button{font-size:140%;position:relative;top:.5px}.log .header .hint{padding-top:2px;font-family:Consolas,monospace;line-height:20px;text-align:left}.log .entries{font-family:Consolas,monospace;flex-grow:1;overflow-y:scroll;background-color:#f0f0f0;border:solid #ccc 1px}.log .entries .entry-wrapper{animation-name:emphasize;animation-duration:10s}@keyframes emphasize{from{background-color:#add8e6}to{background-color:inherit}}.log .entries .entry{padding-left:2px;padding-right:2px;white-space:pre;cursor:default}.log .entries .entry[data-is-highlighted=true]{background-color:#ff0}.log .simple-button{display:inline-block;padding:1px 3px 0 3px;margin-bottom:2px;height:13px;line-height:12px;font-size:12px;border:solid #aaa 1px;border-radius:4px;user-select:none;background-color:#fff}.log .simple-button:hover{background-color:#ddd}.lifecycle-panel{padding:2px 0 2px 0}.lifecycle-panel-inner{font-family:Consolas,monospace;display:inline-flex;flex-shrink:0;flex-direction:column;align-items:flex-start;background-color:#dde;padding:4px;border:solid #888 1px;border-radius:4px;position:relative}.lifecycle-panel-inner .component-instance{color:#666;position:absolute;top:1px;right:0px;padding-right:4px}.lifecycle-panel-inner .lifecycle-method::BEFORE{content:"-"}.lifecycle-panel-inner .lifecycle-method{border-radius:4px}.lifecycle-panel-inner .lifecycle-method[data-is-implemented=false]{color:#666}.lifecycle-panel-inner .lifecycle-method[data-is-highlighted=false]{background-color:inherit;transition:background-color .3s}.lifecycle-panel-inner .lifecycle-method[data-is-highlighted=true]{background-color:#ff0}